.App {
  text-align: center;
  min-height: 100vh;
  background-color: #fff;
  margin: 0 auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.bone-md
{
  height: 256px;
  width: 256px;
}
.mint
{
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 48px;
  margin: 36px !important;
}
.mint-price ul
{
  text-align: left;
  padding: 12px 0px;
}
.mint-price li
{
  padding: 1.5em 1em;
}
.mint-cnt
{
  justify-self:center;
  align-self:center;
  width: 100%;
}
.mint-cnt img
{
  margin: 2em;
}
.mint-btn
{
  margin-top: 1em;
}
select
{
  max-width: 50%;
}
.roadmap
{
 width: 80%;
}
